
.user-page .box-header {
		height:46px;
		line-height:46px;
		color:#444;
		display:block;
		padding:0 0 0 18px;
		position:relative;
		border:1px solid #d9d9d9;
		margin:0 0 5px 0;
		background-color:#fff;
}
.user-page .leftbtn {
		float:left;
}
.user-page .rightinput {
		float:right;
		margin-right:10px;
}
.user-page .rightinput input {
		border:1px solid #1970cf;
		height:28px;
		border-radius:15px;
		box-shadow:none !important;
		width:180px;
		padding-left:10px;
}
.user-page .rightinput input:active {
		box-shadow:none !important;
		outline:none;
}
.user-page .rightinput input:focus {
		box-shadow:none !important;
		outline:none;
}
.user-page .searchBtn {
		border:none;
		background-color:white;
		margin-left:-40px;
		box-shadow:none;
		height:20px !important;
		outline:none;
}
.user-page .glyphicon-chevron-down {
		margin-left:30px;
		width:28px;
		height:28px;
		border-radius:50%;
		outline:none;
		background-color:white;
		border:1px solid #1970cf;
}
#el_table_wrap .el-button{
		padding:0;
}
.midUser .el-dialog .el-dialog__body{
		max-height: 600px;
		overflow: auto
}
.midUser .el-row:last-child{
		padding-bottom: 20px
}
.midUser .el-col{
	   height:50px;
}
.logoUP .avatar-uploader .el-upload {
	    border: 1px dashed #C0CCDA;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	    top:-1px;
	   /* left:-150px;*/
}
.logoUP  .avatar-uploader .el-upload:hover {
	    border-color: #20a0ff;
}
.logoUP  .avatar-uploader-icon {
	     font-size: 14px;
	     color: #8c939d;
	     width: 135px;
	     line-height: 30px;
	     display: block;
}
.logoUP  .avatar {
	    width: 135px;
	    height: 30px;
	    display: block;
}
@media (min-width:1600px) {
.midUser .el-dialog--small {
		    width: 40%;
}
}
@media (min-width:1300px) and (max-width:1599px){
.midUser .el-dialog--small {
		    width: 58%;
}
}
@media (min-width:1200px) and (max-width:1299px){
.midUser .el-dialog--small {
		    width: 58%;
}
}
@media (max-width:1199px) {
.midUser .el-dialog--small {
		    width: 50%;
}
} 



