
.carTeamList{
    border: 1px solid #cccccc;
    background:white;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}
.unlockPanel{
    border: 1px solid #cccccc;
    background:white;
    padding-left: 0px;
}
.filterInput{
    height: 4%;
}
.carTeamList .el-tree {
    height: 96%;
    overflow-y:auto;
}
.carTeamList .el-tree-node__content{
    border-bottom: 1px solid #ccc;
}
.icon{
    margin-right: 5px;
}
.carInfoBox{
    width: 100%;
    height: 50%;
    border-bottom: 1px solid #cccccc;
    position: relative;
}
.unlocking ul{
    margin-left: 40px;
}
.unlocking ul li{
    height: 40px;
    font-size: 16px;
    font-weight: bold;
}
.carInfoBox p{
    position: absolute;
    left:30px;
    top: -50px;
    font-size: 20px;
    font-weight: bold;
    color:#005fc6;
}
.carInfoBox #map-component #map-tool{
    display: none!important;
}
.unlocking{
    width: 100%;
    height: 50%;
    position: relative;
}
.unlocking-btn{
    margin-left: 20px;
    margin-top: 20px;
}
.lockingDialog .el-dialog{
    width: 150px;
    height: 150px;
    top:50%!important;
    margin-top: -75px;
}
.lockingDialog .el-dialog .el-dialog__body{
    padding: 0px;
}
.lockingDialog .el-dialog ul li {
    width: 150px;
    height: 150px;
    text-align: center;
}
.lockingDialog .el-dialog ul li .el-loading-spinner{
    margin-top: -60px;
}
.lockingDialog .el-dialog ul li  .circular{
    width: 100px;
    height: 100px;
}
.lockingDialog .el-dialog ul li .el-progress-circle{
    width: 100px!important;
    height: 100px!important;
    margin-top: 10px;
}
.lockingDialog .el-dialog ul li .el-icon-check{
    font-size: 40px;
}
.lockingDialog .el-dialog__header{
    display: none;
}
.noclick{
    cursor: not-allowed;
}
