
.map-template {
	width:100%;
	height:100%;
	overflow:hidden;
}
#map-wrapper {
	position:relative;
	width:100%;
	height:800px;
	border:1px solid #8a9eac;
}
#map-container {
	width:100%;
	height:100%;
}

/******地图内车辆列表*********/
.map-carlist {
	position:absolute;
	top:100px;
	left:15%;
	z-index:1031;
	min-width:280px;
}
.carlist-content {
	height:250px;
}
.map-carlist .panel-title, .map-fence .panel-title {
	cursor:move;
}

/*****围栏编辑面板样式*******/
.map-fence {
	position:absolute;
	top:100px;
	left:25%;
	z-index:1040;
}
.map-fence .panel-body {
	min-width:510px;
	padding-right:35px;
}
.map-fence ul li {
	height:35px;
	line-height:35px;
}
.map-fence label {
	min-width:70px;
	text-align:right;
}
.map-fence .textarea-wrap {
	height:65px;
	line-height:65px;
}
.map-fence .textarea-wrap label {
	float:left;
}
.map-fence .textarea-wrap textarea {
	height:100%;
	width:388px;
	resize:none;
}
.map-fence .address-input {
	width:388px;
}
.map-select-wrap {
	position:relative;
	display:inline-block;
	margin-left:-4px;
}
.map-fence input {
	width:157px;
	height:24px;
	line-height:25px;
	border:1px solid #a9a9a9;
}
.map-select {
	width:157px;
	height:24px;
	line-height:25px;
	border:1px solid #a9a9a9;
	text-align:left;
	background-color:#fff;
	font:14px/1.5 \5FAE\8F6F\96C5\9ED1, arial, \5b8b\4f53;
}
.map-fence .dropdown-menu a, .show-fence .dropdown-menu a {
