/******错误提示*********/
.errorShow {
	position:absolute;
	z-index:1300;
	top:50px;
	left:50%;
	height:35px;
	line-height:35px;
	padding:0 10px;
	border-radius:5px;
	text-align:center;
	min-width:280px;
	margin-left:-140px;
	background-color:rgba(0, 0, 0, .5);
	color:#fff;
}

/******回放控制面板*********/
.fatherProgress {
	position:absolute;
	left:10px;
	bottom:60px;
	font-size:12px;
	padding:8px;
	border:1px solid #fff;
	border:1px solid #cdcdcd;
	border-radius:3px;
	box-shadow:0 1px 5px #666;
	background:#f8f8f8;
}
.checkbox-control {
	height:25px;
	line-height:25px;
}
.checkbox-control label {
	padding:0 7px;
}
.checkbox-control label input {
	display:block;
	float:left;
	margin-top:6px;
}
.checkbox-control span {
	display:block;
	float:left;
}
.amountKilo {
	line-height:25px;
	border-bottom:1px solid #cccccc;
}
.fatherProgress .progress {
	position:relative;
	margin-top:15px;
	margin-bottom:10px;
	overflow:inherit;
}
.no-transition {
	transition-duration:0s;
}
.progress-yuan {
	position:absolute;
	z-index:1001;
	top:-2px;
	margin-top:-4px;
	border-radius:100%;
	width:17px;
	height:17px;
	cursor:move;
	background-color:#F8F8F8;
	border:1px solid #00c0ef;
	box-shadow:0 1px 1px 1px rgba(0, 0, 0, .3);
}
.f-m-a {
	margin:0 auto;
	float:initial;
}
.progress-grenn {
	background-color:#00e765 !important;
}
.play-speed {
	padding-bottom:3px;
}
.control-panel {
	display:inline-block;
	width:30px;
	height:30px;
	line-height:30px;
	line-height:30px;
	color:#ccc;
	border:1px solid #FFF;
	border-radius:50%;
	transition:all ease .15s;
	cursor:pointer;
	background-color:#ccc;
	background-image:url(/static/img/all_icon.png);
	background-repeat:no-repeat;
}
.control-panel:hover {
	background-color:#00e765;
}
.btn-active {
	background-color:#70efa5;
}
.xj-p-play {
	background-position:-59px -468px;
}
.xj-p-pause {
	background-position:-91px -468px;
}
.xj-p-stop {
	background-position:-121px -468px;
}
.xj-p-fast {
	background-position:-150px -468px;
}
.xj-p-slow {
	background-position:-30px -468px;
}
.xj-p-backward {
	background-position:0 -468px;
}
.xj-p-forward {
	background-position:-180px -468px;
}

/*地图工具样式*/
#map-tool {
	height:40px;
	width:100%;
	padding:10px 5px;
	background-color:#fff;
