	position:absolute;
	width:14px;
	height:21px;
	right:1px;
	top:1px;
	cursor:pointer;
	background-color:#fff;
}
.halving-r-line:after {
	content:"";
	width:1px;
	background-color:#dbdbdb;
	height:16px;
	position:absolute;
	right:0;
	top:2px;
}
