
.el-table__empty-block{
	width:100% !important;
}
.block {
	float:left;
	margin-left:20px;
}
.block:nth-child(1) {
	margin-left:0;
}
.box-header {
	height:46px;
	line-height:46px;
	color:#444;
	display:block;
	padding:0 0 0 18px;
	position:relative;
	border:1px solid #d9d9d9;
	margin:0 0 5px 0;
	background-color:#fff;
}
.search-input-wrap {
	float:left;
	margin:9px 0 0 10px;
}
.search-input {
	float:left;
	width:120px;
	height:28px;
	border:1px solid #e6e6e6;
	line-height: 28px;
}
.operatExp {
	float:left;
	font-size:12px;
}
.get-carList-dialog {
	float:left;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-pack:center;
	    justify-content:center;
	-ms-flex-align:center;
	    align-items:center;
	width:28px;
	height:28px;
	border:1px solid #e6e6e6;
	border-right:none;
	text-align:center;
}
.chartContainer{
	width: 100%;
	height: 400px;
	padding-right: 14px;
	background: #ffffff;
}
.tireChart{
	height: 100%;
	width: 100%;
}
.mapcontainer{
	width: 30%;
	height: 100%;
	float: right;
	visibility: hidden;
}
.tire{
	display: inline-block;
	width: 16px;
	height: 16px;
	line-height:16px;
	border-radius:3px;
	margin-right: 4px;
	text-align:center;
	background-color:#c0dbf9;
	color:#506ef7;
}
.mymodalCommon{
	position: absolute;
	z-index:999;
	display:block;
	border: 1px solid #ccc;
	visibility:hidden;
}
.mymodalCommon .modal-header{
	position: absolute;
	top:1px;
	right: 0;
	z-index:1;
	padding:0;
	height:21px;
	background:#fff;
}
.chartModal{
	height: 400px;
	width:800px;
	top:50%;
	left: 50%;
	margin-left:-400px;
	margin-top: -200px;
}
.fullScreen{
	top:0;
	width:100%;
}
.mymodal1,.show{
	visibility:visible;
}
.closeMymodalBtn{
	height: 20px;
	width: 20px;
	border: none;
	font-size:14px;
	background:#fff;
}
.humiture{
	display:inline-block;
}
.humitureTxt{
	display:inline-block;
	min-width:60px
}
.showPosition{
	position:absolute;
	top:1px;
	right:0;
}
.positionImg{
	position:absolute;
	top:50%;
	left:50%;
	width:600px;
	height:250px;
	margin-left:-300px;
	margin-top:-125px;
	background:url("/static/img/position-tem.png") no-repeat;
	background-size:cover;
}
.bounce-enter-active {
	animation: bounce-in .5s;
}
.bounce-leave-active {
	animation: bounce-out .5s;
}
@keyframes bounce-in {
0%   {transform: translateX(-600px)
}
100% {transform: translateX(0)
}
}
@keyframes bounce-out {
0%  {transform: translateX(0)
}
100%{transform: translateX(1000px)
}
}






