
li:hover {
	cursor:pointer;
}
.icon {
	display:inline-block;
	margin-right:10px;
	vertical-align:middle;
}
.halo-tree {
	font-size:14px;
	min-height:20px;
	border-radius:4px;
}
.node-selected {
	background-color:#ddd
}
.halo-tree .label{
	color:#666;
}
.halo-tree .txt-company{
	color:#000;
}
.halo-tree .txt-carTeam{
	color:#333;
}
.halo-tree .sub-label{
	color:#005fc6;
}
.halo-tree li {
	margin:0;
	position:relative;
	list-style:none;
}
.halo-tree li > span,
.halo-tree li > i,
.halo-tree li > a {
	line-height:12px;
	vertical-align:middle;
}
.halo-tree ul ul li:hover {
	background:rgba(0, 0, 0, .035)
}
.halo-tree li:after,
.halo-tree li:before {
	content:'';
	left:-8px;
	position:absolute;
	right:auto;
	border-width:1px
}
.halo-tree li:before {
	border-left:1px dashed #999;
	bottom:50px;
	height:100%;
	top:-12px;
	width:1px;
}
.halo-tree li:after {
	border-top:1px dashed #999;
	height:20px;
	top:14px;
	width:12px
}
.halo-tree li span {
	display:inline-block;
	padding:4px 3px;
	text-decoration:none;
	border-radius:3px;
}
.halo-tree li:last-child::before {
	height:26px
}
.halo-tree > ul {
	padding-left:0
}
.halo-tree ul ul {
	padding-left:15px;
}
.halo-tree li.leaf {
	padding-left:4px;
}
.halo-tree li.leaf:after {
	content:'';
	left:-8px;
	position:absolute;
	right:auto;
	border-width:1px;
	border-top:1px dashed #999;
	height:20px;
	top:14px;
	width:12px;
}
.check {
	display:inline-block;
	position:relative;
	top:4px;
}
.halo-tree .icon {
	margin-right:0;
}
.halo-tree .tree-close {
	width:12px;
	height:12px;
	background-size:cover;
	background-image:url("/static/img/treeOpen-1.png");
}
.halo-tree .tree-open {
	width:12px;
	height:12px;
	background-size:cover;
	background-image:url("/static/img/treeClose-2.png");
}
.halo-tree .search {
	width:10px;
	height:10px;
	background-size:cover;
	background-image:url("/static/img/search.png");
}
.check.notAllNodes {
	-webkit-appearance:none;
	-moz-appearance:none;
	-ms-appearance:none;
	width:13px;
}
.inputCheck {
	display:inline-block;
	position:relative;
}
.inputCheck.notAllNodes:before {
	content:"";
	display:inline-block;
	position:absolute;
	width:100%;
	height:100%;
	z-index:10;
	top:50%;
	left:50%;
	transform:translate3d(-50%, -20%, 0);
	/*background-image: url("/../../assets/img/half.png");*/
	background-image:url("../../../static/img/half.jpg");
}
.carChoiceList-icon {
	display:inline-block;
	width:14px;
	height:14px;
}
.ChoiceListIcon-0a {
	background-position:-131px -76px;
}
.ChoiceListIcon-1a {
	background-position:-145px -76px;
}
.ChoiceListIcon-2a {
	background-position:-159px -76px;
}
.ChoiceListIcon-3a {
	background-position:-173px -76px;
}
.ChoiceListIcon-4a {
	background-position:-187px -76px;
}
.ChoiceListIcon-5a {
	background-position:-117px -76px;
}
.ChoiceListIcon-0b {
	background-position:-131px -90px;
}
.ChoiceListIcon-1b {
	background-position:-145px -90px;
}
.ChoiceListIcon-2b {
	background-position:-159px -90px;
}
.ChoiceListIcon-3b {
	background-position:-173px -90px;
}
.ChoiceListIcon-4b {
	background-position:-187px -90px;
}
.ChoiceListIcon-5b {
	background-position:-117px -90px;
}
.ChoiceListIcon-0c {
	background-position:-131px -62px;
}
.ChoiceListIcon-1c {
	background-position:-145px -62px;
}
.ChoiceListIcon-2c {
	background-position:-159px -62px;
}
.ChoiceListIcon-3c {
	background-position:-173px -62px;
}
.ChoiceListIcon-4c {
	background-position:-187px -62px;
}
.ChoiceListIcon-5c {
	background-position:-117px -62px;
}



