
.vehicleList{
  height: 100%;
  padding-left:16px;
  width:390px;
  float: left
}
.configResult{
   float:right;
   width:80px;
   height:36px;
   background:white;
   border-left:1px solid #ccc;
   text-align: center;
}
.configPanel{
  border: 1px solid #cccccc;
  background:white;
  padding-left: 0px;
  height: 100%;
  margin-left: 395px;
  margin-right: 20px;
}
.configPanel header{
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #cccccc;
}
.configPram{
    width: 740px;
    height: 150px;
    margin: 50px auto ;
}
.saveConfig{
    position: absolute;
    bottom:120px;
    width: 100%
}
@media screen and (max-width: 1370px) {
.carImgContainer img{
    width: 500px;
}
}
.configPram ul li{
  width: 185px;
  height: 140px;
  box-sizing: border-box;
  border-right: 1px solid #cccccc;
  float: left;
}
.configPram ul li h1{
  text-align: center;
  font-size: 26px;
  color:#005fc6;
  margin-top: -20px;
}
.configPram ul li p{
  padding-left: 5px;
  line-height: 24px;
  font-weight:bold;
}
.configPram ul li input{
  width: 90px;
  margin-left: 5px;
}
.configCar{
   line-height: 50px;
   padding-left: 20px;
   font-size: 20px;
   font-weight: bold;
}
.carImgContainer{
}
.carImgContainer img{
  display: block;
  margin: 0px auto;
}



