
@media (max-width:1280px) {
.text-nowrap-300 {
			display:block;
			max-width:300px;
			word-break:keep-all;
			white-space:nowrap;
			overflow:hidden;
			text-overflow:ellipsis;
}
}
.text-nowrap-300{
		padding-left:5px;
}
.map-template {
		position:relative;
}
#map-template input::-webkit-input-placeholder {
		padding-left:7px;
}

	/*表格样式*/
.list-active {
		background-color:#d2d2e6;
}
.list-active td {
		background-color:#d2d2e6;
}
#contentList {
		height:35px;
		position:relative;
		z-index:1000;
		overflow:hidden;
}
#contentList .nav > li {
		transition:0.3s ease;
}
#contentList .nav > li a {
		height:35px;
		line-height:35px;
		padding:0 15px;
		margin:0;
		border:none;
		text-transform:uppercase;
		cursor:pointer;
		color:#fff;
}
#contentList .nav > li.active a, #contentList .nav > li a:hover, #contentList .nav > li > a:focus > a {
		background-color:#719ff5;
		color:#fff;
		border-radius:0;
}
[ripple] {
		z-index:1;
		position:relative;
		overflow:hidden;
}
[ripple] .ripple {
		position:absolute;
		background:#FFFFFF;
		width:12px;
		height:12px;
		border-radius:100%;
		animation:ripple 1.6s;
}
@keyframes ripple {
0% {
			transform:scale(1);
			opacity:0.2;
}
100% {
			transform:scale(40);
			opacity:0;
}
}
.contentList-header {
		position:relative;
		z-index:50;
		background:#4285F4;
}
.contentList-header .border {
		position:absolute;
		z-index:2;
		bottom:0;
		left:0;
		background:#F4B142;
		width:auto;
		height:2px;
		transition:0.3s ease;
}
.close-Menu {
		position:absolute;
		top:0;
		right:0;
		font-size:20px;
}
.active-Menu, .close-Menu:hover {
		background-color:#719ff5;
}
.close-Menu a {
		display:block;
		width:35px;
		height:34px;
		line-height:34px;
		left:35px;
		text-align:center;
		color:#fff;
}
.full-menu {
		right:35px;
}
.open-menu {
		right:70px;
}
.full-menu a i {
		display:inline-block;
		width:16px;
		height:16px;
}
.restore-btn {
		background-position:-16px 0;
}
.customFixedTable ul {
		width:100%;
		height:30px;
		text-align:center;
		margin-bottom:0;
}
.customFixedTable ul li {
		float:left;
		height:30px;
		line-height:30px;
		font-size:12px;
		background-color:#bfdefb;
		overflow:hidden;
}
.customFixedTable ul li span {
		display:inline-block;
		line-height:15px;
}
.customFixedTable td {
		font-size:12px;
}
#contentList .table-wrap {
		height:155px;
		background-color:#fff;
		text-align:center;
}
#contentList td {
		height:30px;
		line-height:30px;
		padding:0;
		border:1px solid #ccc;
		overflow:hidden;
		white-space:nowrap;
		word-break:keep-all;
}

	/*
		#contentList tbody td span{
			padding: 0 2px;
		}
	*/
.tyre-state {
		display:inline-block;
		padding-right:5px;
}
.square-number {
		display:inline-block;
		padding:0 2px;
		height:15px;
		line-height:15px;
		border:0;
		border-radius:3px;
		background-color:#FF9900;
		color:#fff;
}

	/*侧边栏样式*/
.show-select-car-list {
		transform:translateX(0);
		transition:all 0.2s linear;
}
.hide-select-car-list {
		transform:translateX(100%);
		transition:all 0.2s linear;
}
#select-car {
		position:absolute;
		right:0;
		top:40px;
		z-index:999;
		width:300px;
		height:92.5%;
		background-color:#fff;
		border:1px solid #d2d2d2;
}
.search-module {
		width:100%;
		height:94.9%;
}
.search-module #lockList{
    margin-bottom: -230px;
    padding-bottom: 230px;
}
.search-module #lockList .el-tree{
    border: none;
}
.tree-li {
		padding-left:16px;
		height:30px;
		line-height:30px;
}
.tree-li a, .tree-li i {
		display:inline-block;
		width:14px;
		height:14px;
}
.tree-li input[type="checkbox"] {
		display:inline-block;
		vertical-align:middle;
		margin-bottom:7px;
}
.child-ul {
		padding-left:36px;
}
#car-list {
		height:25%;
		margin-top:5px;
}
.car-info {
		height:85%;
}
.operate-module {
		position:absolute;
		left:-15px;
		top:39%;
		z-index:1500;
		width:15px;
		height:76px;
		cursor:pointer;
		background:url("/static/img/show_carList_btn.png") no-repeat center;
}
#tree-footer {
		position:relative;
		padding-top:25px;
		margin:0;
		height:260px;
}
#tree-footer .top-border {
		padding-top:15px;
		border-top:1px solid #8c8c8c;
		border-bottom:none;
}
#tree-footer .tree-footer-title {
		position:absolute;
		top:14px;
		left:105px;
		z-index:10;
		height:25px;
		background-color:#fff;
}
#tree-footer li {
		height:36px;
}
#tree-footer label {
		width:80px;
		font-size:12px;
}
#tree-footer input, #tree-footer select {
		width:150px;
		color:#666;
}
.position-line{
		position:absolute;
		height:5px;
		width:100%;
		top:0;
		z-index:1500;
		background-color:transparent;
		cursor:row-resize;
}
.tire-alarm td{
		line-height:18px!important;
		cursor:pointer;
}
.lineChart{
		height:100%;
		width:100%;
}
.popover-li{
		width:25%;
		float:left;
		font-size:14px;
}
.popover-li .title{
		text-align:center;
		font-size:20px;
		color:#005fc6;
}
.popover-li p span{
		display:block;
		float:left;
		width:50%;
		text-align:center;
}
.popover-img{
		width:80%;
		height:150px;
		margin-left:10%;
		margin-top:20px;
}
.img-tem{
		background:url("/static/img/position-tem.png") no-repeat;
		background-size:contain;
}
.img-tire{
		background:url("/static/img/position-tire.png") no-repeat;
		background-size:contain;
}
.humiture-row{
		height:30px;
		line-height:30px;
		width:100%;
		cursor:pointer;
}
.popover-table th{
		text-align:center;
}
.green-light{
		display:inline-block;
		width:14px;
		height:14px;
		background-position:-198px -22px;
}
.red-light{
		display:inline-block;
		width:14px;
		height:14px;
		background-position:-212px -22px;
}
.lock-state{
		display:block;
		width:20px;
		height:20px;
		margin:3px auto;
}
.normal-lock{
		background-position:-266px -18px;
}
.abnormal-lock{
		background-position:-286px -18px;
}
.normal-unlock{
		background-position:-306px -18px;
}
.abnormal-unlock{
		background-position:-326px -18px;
}



