.el-date-editor .el-input__icon {
	left:0;
}
.el-date-editor .el-input__inner {
	padding-left:35px;
	border-radius:0;
	border-color:#e6e6e6;
	margin-left:10px;
}
.el-date-editor--daterange .el-input__icon + .el-input__inner {
	padding-right:10px;
	height:28px;
	width:195px;
	line-height:28px;
	font-size:12px;
}
.el-date-editor--datetimerange .el-input__icon + .el-input__inner {
	width:325px;
	padding-right:10px;
}

/*el-tabs 选项卡(后期可放入main.css)*/
.el-tabs__header {
	margin:0;
}
.el-tabs__active-bar {
	background-color:#005fc6;
}
.el-tabs__item {
	height:36px;
	line-height:36px;
}
.el-tabs__nav-wrap {
	background-color:#fff;
}
.el-tabs__item.is-active {
	color:#333;
}
label.el-checkbox {
	margin-bottom:0;
}



