
ul li {
    list-style:none;
}
.mycontainer {
    width:100%;
    padding:0px;
    margin-top:5px;
    height:100%;
    overflow-y:scroll;
    overflow-x:hidden;
    font-family:'微软雅黑', 'Helvetica Neue', Helvetica, Arial, sans-serif !important;
}
.sortable {
    padding-left:10px;
    margin-bottom:0px;
}
.carstatus-container {
    padding-left:0px;
    padding-right:10px;
}
.panel {
    margin-bottom:10px;
}
.chart-title {
    font-family:'微软雅黑', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size:18px !important;
    font-weight:bold !important;
    cursor: pointer;
}
.park {
    height:74px;
    margin-bottom:10px;
    padding:5px;
    border-radius:5px;
    color:#fff;
}
.park .left-icon{
    position:absolute;
    left:8%;
    top:14px;
    width:46px;
    height:46px;
}
.park:hover {
    box-shadow:6px 6px 2px #cccccc;
    cursor:move;
}
.park .text{
    text-align:right;
}
.park .number{
    margin-left:35%;
    font-size:28px;
    font-family: "Helvetica Neue";
    line-height:34px;
}
.carstatus {
    margin-left:35%;
    font-size:14px;
    line-height:24px;
}
.tagMore{
	  position: absolute;
	  bottom: -24px;
	  z-index: 809;
	  width:100%;
	  height: 24px;
	  margin-left:-10px;
	  display:block;
	  text-align: center;
	  cursor: pointer;
	  background: url('/static/img/carIcon/more.png') no-repeat center
}
  /*车辆状态*/
.FHaveDataCount {
    border:3px solid #005fc6;
    color:#005fc6;
}
.FHaveDataCount .left-icon{
    background-position:0 -279px;
}
.FCouplingCount {
    border:3px solid #035682;
    background:#035682;
}
.FCouplingCount .left-icon{
    background-position:-46px -279px;
}
.FUnCouplingCount {
    border:3px solid #498ca6;
    background:#498ca6;
}
.FUnCouplingCount .left-icon{
    background-position:-92px -279px;
}
.FOnLineCount {
    border:3px solid #5b9fd4;
    background:#5b9fd4;
}
.FOnLineCount .left-icon{
    background-position:-138px -325px;
}
.FOffLineCount {
    border:3px solid #b4b3b3;
    background:#b4b3b3;
}
.FOffLineCount .left-icon{
    background-position:-184px -325px;
}
.FRunningCount {
    border:3px solid #42a742;
    background:#42a742;
}
.FRunningCount .left-icon{
    background-position:0 -325px;
}
.FStopCount {
    border:3px solid #e69a29;
    background:#e69a29;
}
.FStopCount .left-icon{
    background-position:-46px -325px;
}
.FContainerCount {
    border:3px solid #6f7ccb;
    background:#6f7ccb;
}
.FContainerCount .left-icon{
    background-position:0 -371px;
}
.FUnContainCount {
    border:3px solid #60b19d;
    background:#60b19d;
}
.FUnContainCount .left-icon{
    background-position:-46px -371px;
}
.FAlarmCount {
    border:3px solid #c93a3a;
    background:#c93a3a;
}
.FAlarmCount .left-icon{
    background-position:-92px -325px;
}
.FRunningOnDayCount {
    border:3px solid #6a967b;
    background:#6a967b;
}
.FRunningOnDayCount .left-icon{
    background-position:-138px -279px;
}
.FStopOnDayCount {
    border:3px solid #bb9360;
    background:#bb9360;
}
.FStopOnDayCount .left-icon{
    background-position:-184px -279px;
}

  /*电子锁&冷链*/
.FLockCloseCount,.FTemperatureNormalCount{
    border:3px solid #035682;
    background:#035682;
}
.FLockCloseCount .left-icon{
    background-position:-92px -371px;
}
.FTemperatureNormalCount .left-icon{
    background-position:0 -417px;
}
.FLockOpenCount,.FTemperatureAbnormalCount{
    border:3px solid #498ca6;
    background:#498ca6;
}
.FLockOpenCount .left-icon{
    background-position:-138px -371px;
}
.FTemperatureAbnormalCount .left-icon{
    background-position:-184px -371px;
}
.FRefrigeratorWorkCount{
    border:3px solid #498ca6;
    background:#498ca6;
}
.FRefrigeratorNotWorkCount{
    border:3px solid #498ca6;
    background:#498ca6;
}
.addstatus {
    border:3px solid #c9c9c9;
    color:#c9c9c9;
}
.addcar {
    text-align:center;
    margin-top:40px;
    font-size:14px;
}
@media (max-width:1400px){
.carstatus {
      line-height:18px;
      font-size:12px;
}
.park .number{
      font-size:24px;
      line-height:30px;
}
}
.deleteStatus {
    float:right;
    text-decoration:none;
    cursor:pointer;
    font-size:18px;
    font-weight:bold;
    display:none;
}
.show {
    display:block;
}
.chart {
    height:480px;
    padding-top:30px;
}
.mapContainer {
    height:440px;
}
.hightchart {
    height:440px;
}
.circle {
    height:250px;
    position:relative;
}
.circle li {
    height:240px;
}
.carRunStopReport {
    height:300px;
}
.selectBox {
    float:right;
    width:226px;
    height:40px;
}
.modal-dialog {
    width:400px;
    margin-top:300px;
}
.modal-title {
    color:#005fc6;
    font-weight:bold;
}
.modal-header {
    height:50px;
}
.modal-body {
    padding:0px 15px;
}
.modal-body ul li {
    height:40px;
    line-height:30px;
    padding:4px;
    border-bottom:1px solid #cccccc;
}
.modal-body ul li input {
    width:16px;
    height:16px;
}
.modal-footer {
    text-align:center;
    padding-top:10px;
    padding-bottom:0px;
    height:50px;
}
.modal-footer button {
    /*width: 40px;*/
}
.sortable {
    position:relative;
}
.addChart {
    cursor:pointer;
    text-align:center;
    line-height:60px;
}
.selectoption {
    width:150px;
    height:20px !important;
}
.selectoption input {
    height:24px !important;
}
.panel-heading label {
    font-size:14px;
}




